import { HttpClient } from '@microsoft/sp-http';
import { AdaptiveCardExtensionContext } from "@microsoft/sp-adaptive-card-extension-base";
import AuthService from '../Auth/AuthService';
import ProfileService from '../Services/ProfileService';


export default class PTOService {
    private static STORAGE_LIFETIME: number = 24 * 60 * 60 * 1000;
    private static context: AdaptiveCardExtensionContext;
    private static userKey: string; 
    private static authMode: string;

    public static async initialize(context: AdaptiveCardExtensionContext, authMode?: string): Promise<void> {
        this.context = context;
        this.userKey = window.btoa(context.pageContext.user.loginName);
        this.authMode = authMode ? authMode : "Secondary";
        await ProfileService.initialize(context, this.authMode);
        return AuthService.initialize(context);
    }

    public static async get(country: string): Promise<any> {
        const storageKey = 'pto:' + this.userKey;
        const storedData = localStorage.getItem(storageKey);
        const myPTOInfo = (null !== storedData && '' !== storedData) ? JSON.parse(storedData) : null;
        if (myPTOInfo !== null) {
            return myPTOInfo.data;
        }
        if (null === myPTOInfo || myPTOInfo.timestamp + this.STORAGE_LIFETIME < Date.now()) {

            try{
                const token = await this.getAuthorizationToken();
                const res = await this.context.httpClient
                    .get(`${PORTAL_API}/ptobalance`, HttpClient.configurations.v1,
                        {
                            headers: [
                                ['accept', 'application/json'],
                                ['Authorization', 'Bearer ' + token]
                            ]
                        });
                const response = await res.json();
                let vacationBalance = 0;
                let asOfDateformatted = "";
                if (response && response.PTOBalance) {
                    if (country === "Argentina" || country === "India") {
                        vacationBalance = response.PTOBalance.PTOBalance + response.PTOBalance.ArgIndiaPTO;
                    } else if (country === "Italy" || country === "Philippines") {
                        vacationBalance = response.PTOBalance.PTOBalance + response.PTOBalance.ItalyPhilVacationBalance;
                    }
                    else {
                        vacationBalance = response.PTOBalance.PTOBalance + response.PTOBalance.VacationBalance;
                    }
                    // Format As Of Date
                    const asOfDate = new Date(response.PTOBalance.AsofDate);
                    const asOfMonth = asOfDate.getMonth() + 1;
                    const asOfDay = asOfDate.getDate();
                    const asOfYear = asOfDate.getFullYear().toString().match(/\d{2}$/);
                    asOfDateformatted = asOfMonth + '/' + asOfDay + '/' + asOfYear;
                }
                const dataToStorage = {
                    "data": {
                        "vacationBalance": vacationBalance,
                        "asOfDate": asOfDateformatted,
                        "chargeability": response.PTOBalance.Chargability.toString()
                    },
                    "timestamp": Date.now()
                };
                localStorage.setItem(storageKey, JSON.stringify(dataToStorage));
                return dataToStorage.data;    

            }catch{
                Promise.reject();
            }                
        }
    }

    public static async getPto(): Promise<any> {
        return ProfileService.get().then((profile) => {
            return PTOService.get( profile.countryhome).then((data) => {
                return data;
            });
        });
    }

    private static async getAuthorizationToken(): Promise<string> {
        if (this.authMode === "Primary") {
            return AuthService.getToken();
        }
        else {
            return AuthService.waitForToken();
        }
    }
}

