import { HttpClient } from '@microsoft/sp-http';
import { AdaptiveCardExtensionContext } from "@microsoft/sp-adaptive-card-extension-base";
import AuthService from '../Auth/AuthService';

export default class PswExpService {
    private static STORAGE_LIFETIME: number = 24 * 60 * 60 * 1000;
    private static context: AdaptiveCardExtensionContext;
    private static userKey: string;
    private static authMode: string;

    public static async initialize(context: AdaptiveCardExtensionContext, authMode?: string): Promise<void> {
        this.context = context;
        this.userKey = window.btoa(context.pageContext.user.loginName);
        this.authMode = authMode ? authMode : "Secondary";
        return AuthService.initialize(context);
    }

    public static async get(): Promise<any> {
        const storageKey = 'pswdexp:' + this.userKey;
        const storedProfile = localStorage.getItem(storageKey);
        const pswInfo = (null !== storedProfile && '' !== storedProfile) ? JSON.parse(storedProfile) : null;
        if (pswInfo !== null) {
            return pswInfo.daysToExpire;
        }
        if (null === pswInfo || pswInfo.timestamp + this.STORAGE_LIFETIME < Date.now()) {
            try{
                const token = await this.getAuthorizationToken();
                const res = await this.context.httpClient
                    .get(`${PORTAL_API}/vpc/profile/pwd`, HttpClient.configurations.v1,
                        {
                            headers: [
                                ['accept', 'application/json'],
                                ['Authorization', 'Bearer ' + token]
                            ]
                        });
                const response = await res.json();                        
                const lastChanged = response.pwdLastSet ? response.pwdLastSet / 1e4 - 1.16444736e13 : 0;
                const ONE_DAY = 1000 * 60 * 60 * 24;
                let ExpiresInDays = 0;
                if (lastChanged) {
                    const passwordChangeDate = new Date(lastChanged);
                    passwordChangeDate.setDate(passwordChangeDate.getDate() + 75);
                    ExpiresInDays = Math.round((passwordChangeDate.getTime() - new Date().getTime()) / ONE_DAY);
                }
                const DaysToExpireDataToStorage = {
                    daysToExpire: ExpiresInDays,
                    timestamp: Date.now()
                };
                localStorage.setItem(storageKey, JSON.stringify(DaysToExpireDataToStorage));
                return ExpiresInDays;  

            }catch{
                Promise.reject();
            }                  
        }
    }

    private static async getAuthorizationToken(): Promise<string> {
        if (this.authMode === "Primary") {
            return AuthService.getToken();
        }
        else {
            return AuthService.waitForToken();
        }
    }
}

